

const stateDefault = { //0xxx
    mangNguoiDung: [
        { taiKhoan: 'nguyenvana', hoTen: 'Nguyn Vn A', matKhau: '123123', email: 'nguyenvana@gmail.com', maLoaiNguoiDung: 'QuanTri', soDienThoai: '0909090909' },
        { taiKhoan: 'tranthibi', hoTen: 'Trn Th B', matKhau: '123123', email: 'tranthib@gmail.com', maLoaiNguoiDung: 'NguoiDung', soDienThoai: '0808080808' },
    ],
    nguoiDungSua: {
        taiKhoan: '',
        hoTen: '',
        matKhau: '',
        email: '',
        maLoaiNguoiDung: 'QuanTri',
        soDienThoai: ''
    }

}
// primitive value: number,string,boolean
// reference value: 0xxx,0yyy

export const quanLyNguoiDungReducer = (state = stateDefault, action) => {

    switch (action.type) {
        case 'XOA_NGUOI_DUNG': {
            //Sao chp mng ngi dng
            let mangNguoiDung = [...state.mangNguoiDung];
            //X l xo
            mangNguoiDung = mangNguoiDung.filter(nd => nd.taiKhoan !== action.taiKhoan);
            //Sau khi xo set li gi tr cho mangNguoiDung
            state.mangNguoiDung = mangNguoiDung;
            console.log('state', state);
            //immutable : Tnh bt bin ca redux i vi object v array
            return { ...state }; //0yyy
        }

        case 'THEM_NGUOI_DUNG': {
            let mangNguoiDung = [...state.mangNguoiDung, action.nguoiDung];

            // state.mangNguoiDung = mangNguoiDung;

            // return {...state}
            return { ...state, mangNguoiDung }
        }

        case 'SUA_NGUOI_DUNG':{
            state.nguoiDungSua = action.nguoiDung;

            return {...state};
        }

        case 'CAP_NHAP_THONG_TIN': {
            let mangNguoiDung = [...state.mangNguoiDung];
            let ndUpdate = mangNguoiDung.find(nguoiDung => nguoiDung.taiKhoan === action.nguoiDung.taiKhoan);
            if(ndUpdate) {
                //Ly gi tr ngi dng thay i (action.nguoiDung)
                // ndUpdate.hoTen = action.nguoiDung.hoTen;
                for(let key in ndUpdate){
                    ndUpdate[key] = action.nguoiDung[key];
                }
            }
            //Sau khi sa cp nht li state
            state.mangNguoiDung = mangNguoiDung;
            return {...state}

        }

        default: return state;
    }
}