


import React, { useEffect, useState } from 'react'
import { Route } from 'react-router-dom'
import HeaderHome from '../../components/HeaderHome/HeaderHome'

export default function HomeTemplate(props) {

  const [widthHeight, setWidthHeight] = useState({
    width: window.innerWidth,
    height: window.innerHeight
  })

  useEffect(() => {
    window.onload = () => {
      //bt s kin windowload 
      setWidthHeight({ width: window.innerWidth, height: window.innerHeight });
    }
    window.onresize = () => {
      setWidthHeight({ width: window.innerWidth, height: window.innerHeight });
    }
    return () => {
      window.removeEventListener('onload');
      window.removeEventListener('onresize');
    }
  }, [])
  //Gn mc nh bin Component l component desktop
  let Component = props.component;

  //Xt nu component mobile c tn ti
  if (props.mobileComponent) {
    //Xt nu mn hnh nh hn = 767
    if (widthHeight.width <= 767) {
      Component = props.mobileComponent;
    }
  }

  return (
    <Route exact path={props.path} render={(propsRoute) => {
      return <div>
        <HeaderHome />
        <Component {...propsRoute} />
      </div>
    }} />
  )
}
