


const stateGameDefault = {
    banChon: true, // true l ti, false l xu
    soBanThang: 10,
    tongSoBanChoi: 10,
    mangXucXac: [
        { diem: 6, img: './img/gameXucXac/6.png' },
        { diem: 6, img: './img/gameXucXac/6.png' },
        { diem: 6, img: './img/gameXucXac/6.png' }
    ],
    
}

export const gameXucXacReducer = (state = stateGameDefault, action) => {

    switch (action.type) {

        case 'DAT_CUOC': {
            state.banChon = action.banChon;
            return { ...state };
        }
        case 'PLAY_GAME': {
            let arrXucXacNN = [];
            //X l to ra 1 mng xc xc ngu nhin  thay th state.mangXucXac
            for (let i = 1; i <= 3; i++) {
                //Mi ln lp s to ra 1 con s ngu nhin t 1 => 6
                let soNgauNhien = Math.floor(Math.random() * 6) + 1;
                //T s ngu nhin to ra 1 object ngu nhin
                let xxnn = { diem: soNgauNhien, img: `./img/gameXucXac/${soNgauNhien}.png` };
                //a object ngu nhin vo mng
                arrXucXacNN.push(xxnn);
            }
            //Cp nht mangXucXac = arrXucXac ngu nhin
            state.mangXucXac = arrXucXacNN;
            //Tnh tng im ca mng ngu nhin
            let tongDiem = arrXucXacNN.reduce((diem, xx, index) => {
                return diem += xx.diem;
            }, 0);
            //Nu tng im b hn 11 v ngi chi chn false th thng || tng im ln hn = 11 v ngi chi chn true
            if ((tongDiem < 11 && !state.banChon) || ((tongDiem >= 11) && state.banChon)) {
                state.soBanThang += 1;
            }

            state.tongSoBanChoi = state.tongSoBanChoi + 1;

            return { ...state };
        }

        default: return state;
    }
}